/**This is my context api data flow management 
 *  layer that helps me to 
 * impliment funtionality to my product 
 * and to get acces to all the functions 
 * that helps me to manage my product 
 * either to the cart, product list ..... 
 */
import React, { Component } from 'react';
import { ProductData, detailProduct } from './ProductData';

// Lets create a new context object
const ProductContext =  React.createContext();

/**
 * NB: our class-base Context Provider MUST SEAT at a higher
 * position of our application AS HIGH AS POSSIBLE so that it can 
 * be accessed anywhere in the application
 * preferably on 
 *  index.js OR App.js
 */


    class ProductProvider extends Component { 
        state = {
            products: [],
            detailProduct: detailProduct,
            cart: [],
            modalOpen: false,
            modalProduct: detailProduct,
            cartSubTotal: 0,
            cartTax: 0,
            cartTotal: 0
        };

        /***ComponentDidMount is a life cyle(runs when the program loads)
         *  method ensures that data aloaded b4 displaying
         */
        componentDidMount(){
            this.setProducts();
        }

        /**SetProduct is a funtion that copies the data 
         * so as not to change the original data in case we
         * may need  it for further uses
         */
        setProducts = () =>{
            let tempProducts = [];
            ProductData.forEach( item => {
                const singleItem = {...item};
                tempProducts = [...tempProducts, singleItem];
            });

            this.setState(() => {
                return {products : tempProducts }
            });

        };

        /**utility method  */
        getItem = (id) => {
            const product = this.state.products.find(item => item.id === id);
            return product;
        }

        handleDetail = (id) =>{
            const product = this.getItem(id);
            this.setState(() => {
                return{
                    detailProduct: product
                }
            })
        };
        addToCart = (id) => {
            let tempProducts = [...this.state.products];
            const index = tempProducts.indexOf(this.getItem(id));
            const product = tempProducts[index];

            product.inCart = true;
            product.count = 1;
            const price = product.price;
            product.total = price;

            this.setState(() => {
                return{ 
                    products: tempProducts,
                    cart: [...this.state.cart, product]
                }; 
            },
            () => {
                this.addTotals();
            }
            
            ) ;

        };
        openModal = id => {
            const product = this.getItem(id);
            this.setState(() => {
                return {
                    modalProduct: product,
                    modalOpen: true
                }
            })
        }

        closeModal = () =>{
            this.setState(() => {
                return{ modalOpen: false}
            })
        }

        // Cart components functions 
    increment = (id) => {
        let tempCart = [...this.state.cart];
        const selectedProduct = tempCart.find(item => item.id === id);

        const  index = tempCart.indexOf(selectedProduct);
        const product = tempCart[index];

        product.count = product.count + 1 ;
        product.total = product.count * product.price;

        this.setState(() =>  {
            return{
                cart: [...tempCart]
            };
        },() => {
            this.addTotals();
        });
    }

        decrement = (id) => {
            let tempCart = [...this.state.cart];
            const selectedProduct = tempCart.find(item => item.id === id);

            const  index = tempCart.indexOf(selectedProduct);
            const product = tempCart[index];
            
            product.count = product.count -1;
            if(product.count === 0){
                this.removeItem(id);
            }
            else{
                product.total = product.count * product.price;
                this.setState(() =>  {
                    return{
                        cart: [...tempCart]
                    };
                },() => {
                    this.addTotals();
                });
            }
        }
        removeItem = (id) => {
            
            let tempProducts = [...this.state.products];
            let tempCart = [...this.state.cart];
            tempCart = tempCart.filter(item => item.id !== id);

            const index = tempProducts.indexOf(this.getItem(id));
            let removedProduct = tempProducts[index];

            removedProduct.inCart = false;
            removedProduct.count = 0;
            removedProduct.total = 0;

            this.setState(() => {
                return {
                    cart: [...tempCart],
                    products: [...tempProducts],
                };
            },() => {
                this.addTotals();
            }
            );
        }

        clearCart = () => {
            
            this.setState(() => {
                return { cart:[] };
            }, () => {
                this.setProducts();
                this.addTotals();
            });
        }
         
        addTotals = () => {
            let subTotal = 0;
            this.state.cart.map(item => (subTotal += item.total));
            const tempTax = subTotal * 0.1;
            const tax = parseFloat(tempTax.toFixed(2));
            const total = subTotal + tax;

            this.setState(() => {
                return{
                    cartSubTotal: subTotal,
                    cartTax: tax,
                    cartTotal: total
                }
            });

        }

        //End Cart components functions 

    render() {
        return (
            <ProductContext.Provider value= {{
                ...this.state,
                handleDetail: this.handleDetail,
                addToCart: this.addToCart,
                openModal: this.openModal,
                closeModal: this.closeModal,
                increment: this.increment,
                decrement: this.decrement,
                removeItem: this.removeItem,
                clearCart: this.clearCart
            }}> 
                {this.props.children}        
            </ProductContext.Provider>
        )
    }
}

// Let create our Product Consumer
const ProductConsumer = ProductContext.Consumer;

export {  ProductProvider, ProductConsumer };




